.shortcut-wrapper {
    // background: linear-gradient(180deg, #FAFAFA 18.36%, rgba(250, 250, 250, 0.2) 82.84%), #EDEFF9;
    padding-top: 20rem;
    position: relative;
    &::before {
        content: "";
        position: absolute;
        left: 0;
        top: 0;
        height: 100%;
        width: 100%;
        z-index: -1;
        opacity: 0.7;
        // background: linear-gradient(180deg, #fafafa 18.36%, rgba(250, 250, 250, 0) 82.84%), #edeff9;
        background: linear-gradient(180deg, rgba(237, 239, 249, 0) 0%, #edeff9 100%);
    }
    .shape {
        &:nth-child(1) {
            left: 0;
            bottom: 20rem;
        }
        &:nth-child(2) {
            left: 32%;
            bottom: 0;
        }
        &:nth-child(3) {
            bottom: 12rem;
            right: 0;
        }
    }
}
.shortcut {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    &-left {
        flex: 0 0 40%;
        max-width: 40%;
        padding-bottom: 7rem;
    }
    &-right {
        flex: 0 0 50%;
        max-width: 50%;
        img {
            height: 100%;
            width: 100%;
            object-fit: cover;
        }
    }

    .text {
        max-width: 52rem;
    }
    .title {
        max-width: 47rem;

    }
    .btn {
        margin-top: 3rem;
        border-radius: 4rem;
        padding: 0 3rem;
    }
}
