.langs {
    position: relative;
    width: max-content;
    border-left: 1px solid rgba($c-white, .2);
    height: 100%;
    display: flex;
    align-items: center;
    @include bp(tablet){
      margin-left: 0;
      margin-bottom: 2rem;
    }
  
    &-menu {
      display: flex;
      cursor: pointer;
      align-items: center;
      justify-content: center;
      position: relative;
      height: 5.6rem;
      padding: 0 1rem;
      transition: $transition;
      
      @include bp(tablet){
        display: none;
      }
  
  
  
      
  
      &:hover, &.active {
        color: $c-black;
        .header-langs {
          &-menu {
            &::after {
              border-top-color: $c-text;
  
            }
  
            &__code {
              opacity: 1;
            }
          }
        }
      }
  
      &.active {
        
  
        .langs-menu-icon {
          transform: scale(1,-1);
        }
  
        & + .langs-menu-content {
          transform: translateY(100%) translateX(-50%);
          opacity: 1;
          pointer-events: all;
          @include bp(tablet){
            transform: unset;
          }
        }
      }
  
      &__img {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 1.8rem;
        width: 1.8rem;
        border-radius: 50%;
        overflow: hidden;
        img {
          object-fit: cover;
          height: 100%;
          width: 100%;
        }
      }
  
    
      &__icon {
        color: rgba($c-black, .5);
        font-size: 2.5rem;
        display: flex;
        transition: $transition;
      }
  
      &-content {
        padding: .5rem;
        bottom: -1px;
        left: calc(50% - 1px);
        position: absolute;
        width: 100%;
        transition: $transition;
        opacity: 0;
        pointer-events: none;
        z-index: 3;
        border: 1px solid $c-border;
        border-radius: 1rem;
  
        transform: translateY(100%) translateX(-50%);
        @include bp(tablet){
          transform: none;
          position: static;
          display: flex;
          align-items: center;
          width: max-content;
          opacity: 1;
          pointer-events: all;
          border-radius: 3.8rem;
          padding: .7rem 1rem;
        }
  
        &__item {
          display: flex;
          align-items: center;
          padding: .5rem .5rem;
          justify-content: center;
          //background: rgba($c-bg, .3);
          border: 1px solid rgba($c-white, .2);
          border-radius: .5rem;
          @include bp(tablet){
            padding-left: 1.5rem;
            padding-right: 1.5rem;
          }
          
          &:hover {
            background: rgba($c-bg, .2);
          }
  
          & + & {
            margin-top: .5rem;
            @include bp(tablet){
              margin-top: 0;
            }
          }
  
          &.active {
            border: 1px solid $c-border;
            .langs-menu__code{
              color: #FFF;
            }
          }
  
          .langs-menu {
            &__img {
              flex-shrink: 0;
            }
          }
        }
      }
    }
  }
  