.hero {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    &-search-row {
    box-shadow: $box-shadow;
        position: relative;
        z-index: 5;
        background: $c-white;
        width: 100%;
        max-width: 80rem;
        display: flex;
        margin-top: 4rem;
        align-items: center;
        padding-right: 1.4rem;
        border: 1px solid $c-border;
        border-radius: 3.8rem;
        .dropdown_bar {
            box-shadow: unset;
        }
        .dropdown-wrapper + .dropdown-wrapper {
            border-left: 1px solid $c-border;
        }
        .btn {
            border-radius: 4.6rem;
            padding: 0 1.8rem;
        }
    }
    .dropdown-row {
        width: 100%;
        max-width: 70rem;
        display: flex;
        gap: 1.2rem;
        margin-top: 2rem;
        position: relative;
        z-index: 4;

    }
    .title {
        letter-spacing: -0.9px;
        max-width: 60rem;
        position: relative;
        z-index: 1;

    }
    .text {
        letter-spacing: $letter-spacing;
        max-width: 70rem;
        position: relative;
        z-index: 1;
    }
}