.title {
  font-style: normal;
  line-height: 1.6;
  color: $c-title;
  font-weight: 500;

  &--second {
    font-family: $font-family-second;
  }

  &--tight {
    letter-spacing: -0.24px;
  }

  &--underlined {
    text-decoration: underline;
  }

  span.colored-black {
    color: $c-black;
  }
  span.colored-green {
    color: $c-green;
  }
  span.colored-purple {
    color: $c-purple;
  }
  span.colored-white {
    color: $c-white !important;
  }

  &--black {
    color: $c-black;
  }
  &--light-black {
    color: rgba($c-black, 0.9);
  }
  &--dark-blue {
    color: $c-dark-blue;
  }

  &--green {
    color: $c-green;
  }
  &--white {
    color: #fff !important;
  }
  &--blue {
    color: $c-blue !important;
  }
  &--gray {
    color: #6a7190 !important;
  }
  &--light {
    color: rgba($c-black, 0.6) !important;
  }
  &--x-light {
    color: rgba($c-black, 0.4) !important;
  }
  &--xxxl {
    font-size: var(--font-size-title--xxxl);
    line-height: 1em;
  }

  &--xxl {
    font-size: var(--font-size-title--xxl);
    line-height: 1.2em;
  }

  &--xl {
    font-size: var(--font-size-title--xl);
    line-height: 1.4em;
  }

  &--lg {
    font-size: var(--font-size-title--lg);
    //line-height: 1.6em;
    line-height: 1.4em;
  }

 

  &--xmd {
    font-size: var(--font-size-title--xmd);
  }

  &--md {
    font-size: var(--font-size-title--md);
  }

  &--sm {
    font-size: var(--font-size-title--sm);
  }

  &--xsm {
    font-size: var(--font-size-title--xsm);
    line-height: 1.6em;
  }

  &--xs {
    font-size: var(--font-size-title--xs);
    line-height: 1.6em;
  }

  &--xxs {
    font-size: var(--font-size-title--xxs);
    line-height: 1.6em;
  }
  &--xxs {
    font-size: var(--font-size-title--xxs);
    line-height: 1.6em;
  }
  &--xxxs {
    font-size: var(--font-size-title--xxxs);
    line-height: 1.6em;
  }
  &--4xs {
    font-size: var(--font-size-title--4xs);
    line-height: 1.6em;
  }

  &--center {
    text-align: center;
  }

  &--w-shape {
    display: flex;
    align-items: center;
    gap: 4rem;

    img {
      height: 5.5rem;
    }
  }
  &--w-icon {
    display: flex;
    align-items: flex-start;
    span {
      order: -1;
    }
    .title__icon {
      font-size: 2rem;
      margin-right: 2.5rem;
      color: #ff9d5f;
    }
  }
}
