.index {
    overflow: hidden;
    padding-top: 5rem;
    &-intro {

        .background {
            max-width: 108rem;
            margin-top: 8rem;
            height: 40rem;
            width: 100%;
            border-radius: 3.6rem;
            background: linear-gradient(44deg, rgba(250, 250, 250, 0.5) 21.43%, rgba(255, 255, 255, 0) 77.21%), #edeff9;
        }
    }
    &-content {
        .job-slider-wrapper {
            .job-slider {
                padding: 9rem 0;
                @include bp(mobile) {
                    padding: 4rem 0;
                }
            }
        }
    }
    .shape {
        &.shape-1 {
            top: 19rem;
            left: 0;
        }
        &.shape-2 {
            top: 35rem;
            right: 0;
        }
        
        
    }
}
