.pagination {
    &-wrapper {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        margin-top: 5rem;
    }
    &-result-text {
        font-size: 1.5rem;
        color: rgba($c-black , .6);
        margin-top: 2rem;
    }
}
.paginationjs {
    font-family: $font-family-base;
}
.paginationjs {
    .paginationjs-pages {
        ul {
            display: flex;
            gap: 1rem;
        }
        li {
            border: none;
            &:first-child {
                & > a {
                    font-size: 3rem;
                    margin-top: -3px;
                }
            }
            &:last-child {
                border: none;
                & > a {
                    font-size: 3rem;
                    margin-top: -3px;
                }

            }

            & > a {
                height: 4rem;
                width: 4rem;
                display: flex;
                align-items: center;
                justify-content: center;
                background: transparent;
                border-radius: 50%;
                font-size: 1.5rem;
                font-weight: 500;
                line-height: 1;
                min-width: unset;
                color: $c-black;
                &:hover {
                    background: transparent;
                    color: $c-green;
                }
            }
            &.active {
                & > a {
                    background: $c-black;
                    height: 4rem;
                    cursor: pointer;
                    &:hover {
                        color: #fff;
                    }
                }
            }
        }
    }
}

