.challenge-detail {
    // height: calc(100vh - 10rem);
    padding-bottom: 20rem;
    .container {
        // padding: 0 17rem;
        max-width: 96rem;
    }
    .subpage-header {
        .link {
            &__icon {
                margin-right: 1.2rem;
            }
        }
    }
    .subpage-content {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        gap: 2rem;
    }
    .challenge-card {
        width: 100%;
        max-width: 40rem;
    }
    .box {
        max-width: 54rem;
        width: 100%;
    }
}