.input {
  outline: none;
  width: 100%;
  color: $c-black;
  font-size: 1.6rem;
  height: 4.8rem;
  padding-left: 1.6rem;
  border: 1px solid $c-border;
  transition: $transition-fast;
  -webkit-appearance: none;
  letter-spacing: $letter-spacing;
  position: relative;
  background: $c-bg;
  border-radius: 0.8rem;
  &.error {
    border-bottom: 1px solid #cc0000 !important;
  }
  &::placeholder {
    color: rgba($c-black, 0.4);
    font-size: 1.6rem;
    letter-spacing: -0.014em;
    @include bp(mobile) {
      font-size: 1.5rem;
    }
  }
  &-wrapper {
    position: relative;
    &.input-error {
      overflow: initial;

      .input {
        border: 1px solid $c-red !important;
      }
      #phone-error {
        display: none !important;
      }
    }
  }
  &::-webkit-outer-spin-button,
  &::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }

  &:focus {
    border: 1px solid $c-green;
  }

  &-title {
    color: $c-black;
    font-size: 1.3rem;
    font-weight: 500;
    letter-spacing: $letter-spacing;
  }

  &-disabled {
    pointer-events: none;
    border-color: transparent;
  }
}
textarea {
  &.error {
    border-bottom: 1px solid #cc0000 !important;
  }
}
label.error {
  margin-bottom: 0;
  margin-top: 15px;
  color: #cc0000 !important;
  font-weight: 600;
  font-size: 1.2rem;
  position: absolute;
  bottom: -2rem;
  left: 0rem;
  line-height: 1em;
  @include bp(mobile) {
    bottom: -1.8rem;
  }
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
  //-webkit-box-shadow: none !important;
  -webkit-box-shadow: 0 0 0 35px white inset !important;
  -webkit-background-clip: text;
}

input:-webkit-autofill {
  -webkit-text-fill-color: $c-black !important;
}

textarea[data-autoheight="true"] {
  overflow: visible;
}

.textarea {
  position: relative;
  margin-bottom: 3rem;
  &.error {
    margin-bottom: 5rem;
  }

  @include bp(mobile) {
    margin-top: 0;
  }

  textarea {
    width: 100%;
    height: 11rem;
    max-height: 18rem;
    border: none;
    border-bottom: 1px solid rgba(#1f263a, 0.15);
    outline: none;
    padding: 2rem 0;
    color: rgba($c-black, 0.7);
    font-size: 1.8rem;
    font-weight: 500;
    resize: none;
    &::placeholder {
      color: rgba($c-title, 0.3);
      font-size: 1.7rem;
      font-weight: 500;
      @include bp(mobile) {
        font-size: 1.5rem;
      }
    }
  }
}
