.box {
    background: #fff;
    border-radius: 2rem;
    border: 1px solid $c-border;
    &-header {
        padding: 2.7rem 3rem;
        padding-bottom: 0;
        display: flex;
        align-items: center;
        gap: 2rem;
        &-icon {
            border-radius: 50%;
            width: 6rem;
            height: 6rem;
            display: flex;
            align-items: center;
            justify-content: center;
            background: radial-gradient(
                    171.87% 76.39% at 82.5% 0%,
                    rgba(255, 255, 255, 0.3) 0%,
                    rgba(255, 255, 255, 0) 100%
                ),
                rgba(190, 68, 68, 0.1);
            color: #be4444;
            font-size: 2.5rem;
        }
        &-title {
            font-size: 1.8rem;
            max-width: 17rem;
        }
    }
    &-content {
        padding: 2.7rem 3rem;

        &-text {
            font-weight: 400;
            margin-bottom: 5rem;
        }
        .btn {
            margin-top: 3rem;
            width: 100%;
            font-size: 1.5rem;
        }
    }
    &-footer {
        padding: 3rem;
        border-top: 1px solid $c-border;
        display: flex;
        justify-content: center;
        &-text {
            font-size: 1.5rem;
            font-weight: 400;
            text-align: center;
            max-width: 35rem;
        }
    }
}
