.section {
    padding-top: 10rem;
    position: relative;

    // &:last-child {
    //     padding-bottom: 10rem;
    // }
    &-head {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 2rem;
        &-left {
            flex: 0 0 70%;
            max-width: 70%;
        }
    }
    &.with-top-border {
        margin-top: 10rem;
        border-top: 1px solid $c-border;
    }
    &-divider-shape {
        position: absolute;
        left: 50%;
        top: 0;
        transform: translate(-50% , -72%);
        z-index: 3;
    }
}
