.find-talent {
    & ~ .shortcut-wrapper {
        padding-top: 0;
    }
    .subpage-intro {
        .background {
            max-width: 108rem;
            margin-top: -7rem;
            height: 48rem;
            width: 100%;
            border-radius: 3.6rem;
            // background: linear-gradient(44deg, rgba(250, 250, 250, 0.5) 21.43%, rgba(255, 255, 255, 0) 77.21%), #edeff9;
            // background: linear-gradient(180deg, rgba(250, 250, 250, 0.6) 23.27%, rgba(255, 255, 255, 0) 48.57%), #edeff9;
            background: linear-gradient(180deg, rgba(250, 250, 250, 0.60) 5.27%, rgba(255, 255, 255, 0.00) 48.57%), #EDEFF9;


        }
    }
    .hero {
        .title {
            max-width: 65rem;
        }
        .dropdown-wrapper {
            display: flex;
            align-items: center;
            justify-content: center;
            height: 7.8rem;
            border-radius: 2.4rem;
            padding: 0 1rem;
            max-width: 32rem;
            margin-bottom: 3rem;
            background: rgba(#EDEFF9, .8);
            .dropdown_bar {
                height: 5.8rem; 
                &-item {
                    height: 5.8rem;
                    border-radius: 1.6rem;
                    &__title {
                        font-weight: 500;
                        color: $c-black;
                    }
                }
            }
        }
    }

    .shape {
        &.shape-1 {
            top: 50rem;
            left: 0;
        }
        &.shape-2 {
            top: 25rem;
            right: 0;
        }
    }
}
