.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  //z-index: 2;
  height: var(--header-height);
  transition: $transition-slow;
  --header-color: #{#4d4d4d};
  max-width: var(--container-width);
  margin: 0 auto;
  padding: 0 var(--container-gutter);
  &-left{
    display: flex;
    align-items: center;
    height: 100%;
    .slogan {
      color: rgba($c-black, .5);
      font-weight: 600;
      font-size: 8.4px;
      max-width: 8rem;
      line-height: 1.2;
    }
  }
  .logo{
    height: 2.8rem;
    object-fit: contain;
    margin-right: .5rem;
    
  }

  &.active{
    .header-right{
      opacity: 1;
      pointer-events: all;
    }
  }

  &-menu {
    display: flex;
    align-items: center;
    gap: 4rem;
    height: 100%;
    margin-left: 5rem;
    @include bp(tablet){
      flex-direction: column;
      height: auto;
      gap: 0;
      align-items: flex-start;
      width: 100%;
    }
    &-link {
      font-size: 1.6rem;
      color: $c-black;
      transition: $transition;
      height: 100%;
      display: flex;
      align-items: center;
      font-weight: 500;
      letter-spacing: -0.155px;
      @include bp(tablet){
        height: 5rem;
        align-items: center;
        width: 100%;
        font-size: 2rem;
      }
      &:hover {
        color: $c-green;
      }
      &.mobile-link{
        @include bp(r-tablet){
          display: none;
        }
      }
    }
  }
  &-right {
    display: flex;
    align-items: center;
    gap: 1.4rem;
    @include bp(tablet) {
      flex-direction: column;
      position: absolute;
      left: 0;
      height: calc(100dvh - var(--header-height));
      top: var(--header-height);
      width: 100%;
      z-index: 3;
      background: $c-bg;
      align-items: flex-start;
      padding: 0 calc(var(--container-pusher) + var(--container-gutter));
      opacity: 0;
      pointer-events: none;
      transition: $transition;
    }
    .link {
      margin-right: 1.4rem;
      height: 100%;
    }
  }
  &-wrapper{
    position: relative;
  }
  .header-menu-opener{
    width: 3rem;
    height: 2.5rem;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: space-between;
    @include bp(r-tablet){
      display: none;
    }
    span{
      display: block;
      height: 2px;
      background: $c-white;
      width: 100%;
      border-radius: 1rem;
      transition: $transition;
    }
    &.active{
      span{
        &:nth-child(2){
          display: none;
        }
        &:nth-child(1){
          transform: translateY(8px) rotate(50deg);
        }
        &:nth-child(3){
          transform: translateY(-10px) rotate(-50deg);
        }
      }
    }
  }
  
}
