.challenge-list {
    &-grid {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-column-gap: 2.3rem;
        grid-row-gap: 2.3rem;
        &-wrapper {
            padding-top: 2rem;
            &.with-top-border {
                margin-top: 5rem;
            }
        }
        .challenge-card {

            &-header {
                &-content {
                    padding-top: 0;
                }
            }
        }
    }
    .shape {
        &.shape-1 {
            top: 11rem;
            left: 0;
        }
        &.shape-2 {
            right: 0;
            top: 11rem;
        }
        &.shape-3 {
            left: 0;
            bottom: 33rem;
        }
        &.shape-4 {
            right: 0;
            bottom: -8rem;
        }
    }
}
