.footer {
  width: 100%;
  position: relative;
  padding-bottom: 4rem;
  padding-top: 7rem;
  border-top: 1px solid $c-border;
  // background: linear-gradient(180deg, rgba(255, 255, 255, 0) 27.31%, #ffffff 100%);

  &--basic {
    border-top: 0;
    padding-top: 0;
    padding-bottom: 0;
    height: 10rem;
    .footer-bottom {
      margin-top: 0;
      padding-bottom: 4rem;
    }
  }

  @include bp(r-desktop-l) {
    max-width: var(--container-width);
    margin-left: auto;
    margin-right: auto;
    background: transparent;
  }
  @include bp(mobile) {
    padding: 5rem 0;
    padding-bottom: 4rem;
  }

  &-content {
    display: flex;
    justify-content: space-between;
    padding-left: calc(var(--container-pusher) + 10rem);
    padding-right: calc(var(--container-pusher) + 10rem);
    
    @include bp(r-desktop-l) {
      padding-left: 0;
      padding-right: 0;
    }
    &-left {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      flex: 0 0 34%;
      max-width: 34%;
      &-item {
        display: flex;
        flex-direction: column;
        gap: 3rem;
      }
      .footer-text {
        max-width: 30rem;
      }
    }
    &-right {
      flex: 0 0 58%;
      max-width: 58%;

      
      
    }
    .logo {
      height: 3rem;
      object-fit: contain;
    }
  }
  &-menu {
    display: flex;
    gap: 8rem;
    width: 100%;
    position: relative;

    @include bp(r-desktop-m) {
      justify-content: flex-start;
      gap: 0;
    }

    &-item {
      display: flex;
      flex-direction: column;
      flex-shrink: 0;
      max-width: 25rem;

      @include bp(r-desktop-m) {
        &:not(:last-child) {
          margin-right: 8rem;
        }
      }
    }

    &__link {
      font-size: 1.5rem;
      color: $c-black;
      line-height: 1.5;
      letter-spacing: $letter-spacing;

      & + & {
        margin-top: 1.2rem;
      }

      &:hover {
        color: $c-green;
      }
    }

    &__title {
      font-size: 1.6rem;
      font-weight: 500;
      color: $c-black;
      margin-bottom: 2.5rem;
      letter-spacing: $letter-spacing;
      line-height: 1.5;

      & + & {
        margin-top: 3.4rem;
      }

      &.footer-menu__link {
        margin-bottom: 0;

        &:hover {
          text-decoration: underline;
        }
      }
    }

    @include bp(tablet) {
      display: none;
    }
  }

  &-contact {
    display: flex;
    flex-direction: column;
    gap: 2rem;
    &-item {
      font-size: 1.7rem;
      display: flex;
      align-items: center;
      gap: 1rem;
      color: $c-black;
      &:hover {
        color: $c-green;
      }
      svg {
        color: rgba($c-gray, 0.6);
      }
    }
  }
  &-copyright {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 6rem;
    margin-top: 12rem;
    padding-left: calc(var(--container-pusher) + 10rem);
    padding-right: calc(var(--container-pusher) + 10rem);
    border-top: 1px solid $c-border;
    position: relative;
    @include bp(r-desktop-l) {
      padding-left: 0;
      padding-right: 0;
    }
    @include bp(tablet) {
      justify-content: center;
    }
    .social {
      position: absolute;
      top: 0;
      left: 50%;
      transform: translate(-50%, -50%);
    }
    &-links {
      display: flex;
      gap: 4rem;
    }
    &-link {
      color: rgba($c-black, 0.5);
      font-size: 1.4rem;
      letter-spacing: $letter-spacing;
      &:hover {
        color: $c-black;
      }
    }
    .text {
      color: rgba($c-black, 0.85);
      font-weight: 400;
    }
  }
  &-bottom {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 1rem;
    padding-top: 4rem;
    margin-top: 6rem;
    padding-left: calc(var(--container-pusher) + 10rem);
    padding-right: calc(var(--container-pusher) + 10rem);
    border-top: 1px solid $c-border;
    position: relative;
    

    p {
      color: rgba($c-black, 0.3);
      font-weight: 600;
      letter-spacing: -0.06px;
      font-size: 1.2rem;
    }
  }
}
