/* Colors */
$c-bg: #FAFAFA;
$c-blue: #278EFF;
$c-white: #ffffff;
$c-light: #E1E7FB;
$c-black: #141417;
$c-dark-black : #1c2c35;
$c-gray: #A1A1A2;
$c-dark-gray: #6A7190;
$c-light-gray: #0303031A;
$c-red: #C92C2B;
$c-red-light: #DA4241;
$c-purple: #bd8fbd;
$c-green: #3FBA85;
$c-dark-green: #39A577;
$c-dark-blue: #007AFF;
$c-light-blue: #DEEEFF;
$c-text: rgba($c-black , .8);
$c-text-light: rgba($c-black,.50);
$c-border: #E5ECEC;
$c-border-white: rgba(#fff, .15);
$c-title: $c-black;
$c-divider: rgba($c-black, .1);




/* Font Families*/
$font-family-base: 'Graphik', sans-serif;
$font-family-second: "Graphik", sans-serif;

/* Box Shadows */
$button-shadow: 0px 1px 3px rgba(0, 0, 0, 0.08), 0px 4px 4px rgba(50, 50, 93, 0.11);
$button-shadow--hover: 0px 7px 14px rgba(50, 50, 93, 0.1), 0px 3px 6px rgba(0, 0, 0, 0.08);
$box-shadow: 0px 10px 20px -15px rgba(143, 143, 163, 0.40);

$box-shadow-card: 0px 40px 50px -20px rgba(3,43,92,0.1);



/* Transitions */

$transition: .3s cubic-bezier(.29, .13, .27, .74);
$transition-normal: .25s ease-in-out;
$transition-fast: .2s cubic-bezier(.29, .13, .27, .74);
$transition-slow: .6s cubic-bezier(.21,1.09,1,.99);


/* Letter Spacing*/

$letter-spacing: -0.14px;

:root {
  --font-size-title--xxxl: 9.2rem;
  --font-size-title--xxl: 4.5rem;
  --font-size-title--xl: 3.4rem;
  --font-size-title--lg: 3rem;
  --font-size-title--md: 2.6rem;
  --font-size-title--sm: 2.4rem;
  --font-size-title--xsm: 2rem;
  --font-size-title--xs: 1.3rem;


  --font-size-toptitle: 1.6rem;




  --font-size-text: 1.6rem;




  --content-color: #{$c-black};




  --header-height: 8rem;
  --container-pusher: calc((100vw - var(--container-width)) / 2);


  --container-width: 140rem;
  --container-medium-padding: 9rem;
  --container-small-width: 145rem;
  --container-medium-width: 150rem;
  --container-xsmall-width: 132rem;

  --container-large-padding : 11rem;
  // 150 * 9px = 1350px -> laptop-s
  @media (min-width: 576px) {
    --container-width: 540px;
  }

  @media (min-width: 768px) {
    --container-width: 720px;
  }
  @media (min-width: 992px) {
    --container-width: 960px;
  }
  @media (min-width: 1200px) {
    --container-width: 1150px;
    //--container-width: 100rem;
    
  }
  @include bp(r-laptop-s) {
    --container-width: 128rem;
  }
  @include bp(r-laptop) {
  }
  @include bp(r-desktop-m) {
  }
  @include bp(r-desktop-m) {
    --container-width: 145rem;

  }
  // @include bp(r-desktop-ml) {
  //   --container-width: 165rem;
  // }
  @include bp(r-desktop-l) {
    --container-width: 160rem;
  }
  @media (min-width: 2561px) {
    //--container-width: 220rem;

  }

  @include bp(desktop-m) {
    --container-xsmall-padding: 13.8rem;
    --container-medium-padding: 9rem;
    --container-small-width: 145rem;
    --container-medium-width: 150rem;
    --container-xsmall-width: 140rem;

  }
  @include bp(laptop) {
  }
  @include bp(laptop-s) {
  }


  @include bp(desktop) {
    --font-size-title--xxl: 5.5rem;
    --font-size-title--xl: 5rem;
    --font-size-title--mlg: 3.4rem;
    --font-size-title--xmd: 3.5rem;
    --font-size-title--md: 3.5rem;
    --font-size-title--sm: 3rem;
    --font-size-title--xsm: 2.2rem;
    --font-size-title--xs: 1.8rem;
    --font-size-title--xxs: 1.6rem;
    --font-size-toptitle: 1.8rem;

    --container-xsmall-padding: 8rem;

  }
  @include bp(tablet) {
    --font-size-title--lg: 3rem;
    --font-size-text: 1.6rem;

    --font-size-title--xxxl: 7.2rem;
    --font-size-title--md: 2.5rem;
    --header-height: 14rem;

  }
  @include bp(tablet-small-under) {
    --font-size-title--lg: 2.4rem;
    --font-size-title--mlg: 2.8rem;
    --font-size-title--md: 2.3rem;
    --font-size-title--sm: 2.1rem;

    --font-size-title--xxl: 4rem;
    --font-size-title--xl: 26px;
    --font-size-title--xmd: 3rem;
    --font-size-title--xsm: 2.2rem;
    --font-size-title--xs: 1.8rem;
    --font-size-title--xxs: 1.6rem;
    --font-size-toptitle: 16px;


  }
  @include bp(tablet-small){
    --font-size-title--xxl: 4.5rem;
    --font-size-title--xl: 4rem;
    --font-size-title--md: 2.5rem;
  }
  @include bp(mobile) {
    --container-pusher: 0px;
    --font-size-title--xl: 3rem;
    --font-size-title--xxxl: 4.5rem;
     --font-size-title--lg: 2.5rem;
    --font-size-title--xxl: 4rem;
    --font-size-text: 1.6rem;
     --font-size-title--sm: 2.2rem;
     --font-size-title--mlg: 2.3rem;
     --font-size-title--xsm: 2rem;





  }
  @include bp(mobile-small) {

  }
  @include bp(mobile-l-small) {

  }
  @include bp(mobile-m-small) {
  }
  @include bp(mobile-x-small) {

  }
}


