.btn {
  width: auto;
  text-align: center;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  height: 4.6rem;
  user-select: none;
  position: relative;
  transition: $transition;
  font-size: 1.4rem;
  font-weight: 500;
  padding: 0 2.2rem;
  cursor: pointer;
  box-sizing: border-box;
  border-radius: 1.2rem;

  &--md {
    height: 5rem;
    font-size: 1.5rem;
  }
  &--sm {
    height: 4rem;
  }
  &__text {
    letter-spacing: -0.145px;
  }

  &--green {
    background: $c-green;
    border-color: $c-green;
    color: $c-black;

    &:hover,
    &:focus {
      background: darken($c-green, 5%);
      border-color: darken($c-green, 5%);
    }
  }

  &--transparent-white {
    background: transparent;
    color: $c-white;
    border: 1px solid $c-green;

    &:hover,
    &:focus {
      background: $c-green;
      border-color: $c-green;
      color: $c-black;
    }
  }

  &--full {
    display: flex;
    width: 100%;
  }

  &--i {
    min-width: unset;
    width: 6rem;
    height: 6rem;
    padding: 0;
    flex-shrink: 0;
    font-size: 2rem;
    @include bp(tablet-small) {
      width: 5rem;
      height: 5rem;
      font-size: 1.8rem;
    }

    .btn {
      &__icon {
        height: 1em;
      }
    }

    &.has-big-icon {
      font-size: 3rem;
    }
  }

  &--white {
    background: #fff;
    color: $c-black;

    &:hover,
    &:focus {
      background-color: darken(#fff, 2%);
      color: $c-black;
    }
  }
  &--black {
    background: radial-gradient(100% 100% at 50% 0, hsla(0, 0%, 100%, 0.3) 0, hsla(0, 0%, 100%, 0) 100%),
      linear-gradient(0deg, #141417, #141417);

    color: $c-white;
    transition: 0.5s;
    background-size: 100%;

    &:hover,
    &:focus {
      background-size: 200%;
    }
  }
  &--dark-black {
    background: $c-black;
    color: $c-white;

    &:hover,
    &:focus {
      background-color: darken($c-black, 5%);
    }
  }
  &-row {
    display: flex;
    align-items: center;
    .btn + .btn {
      margin-left: 2rem;
    }
  }

  &--i {
    &-left,
    &-right {
      min-width: unset;
      .btn__icon {
        font-size: 1.4rem;
      }
    }

    &-left {
      .btn {
        &__icon {
          margin-right: 1.2rem;
        }
      }
    }

    &-right {
      .btn {
        &__icon {
          margin-left: 1.2rem;
        }
      }
    }
  }

  &__icon {
    transition: $transition;
  }
}

.loadmore {
  display: flex;
  justify-content: center;
  align-items: center;
}
