.social {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  font-size: 2rem;

  &__item {
    width: 5rem;
    height: 5rem;
    background: $c-white;
    border-radius: 50%;
    border: 1px solid $c-border;
    cursor: pointer;
    color: $c-black;
    display: flex;
    flex-shrink: 0;
    align-items: center;
    justify-content: center;
    transition: $transition;
    &:hover{
      color: $c-green;
    }

    & + & {
      margin-left: 1.6rem;
     
    }

    


  }
}
